.projects-section{
    text-align: center;
    background-color: #f5f5f5;
    padding: 40px 0;
}

.card-title{
    font-size: 20px;
    font-weight: bold;
    color: #00A7E1;
}

.card-text{
    font-size: 15px;
    text-align: justify;
    margin-top: 20px;
}

.tags{
    font-size: 13px;
    font-weight: 600;
    color: #023262;
}

.project a{
    color: #011C40;
}

.project a:hover{
    color: #88DFF2;
    font-weight: 900;
    text-decoration: none;
}